import { DataAttribute, MetadataAttribute } from './dataAttributes';
import { isInsideIFrame } from './iframe';
export var HighlightType;

(function (HighlightType) {
  HighlightType["None"] = "";
  HighlightType["Element"] = "element";
  HighlightType["ContentComponent"] = "content-component";
  HighlightType["ContentItem"] = "content-item";
})(HighlightType || (HighlightType = {}));

var ElementSelector = "*[".concat(DataAttribute.ElementCodename, "]");
var ContentComponentSelector = "*[".concat(DataAttribute.ComponentId, "]:not([").concat(DataAttribute.ElementCodename, "])");
var ContentItemSelector = "*[".concat(DataAttribute.ItemId, "]:not([").concat(DataAttribute.ComponentId, "]):not([").concat(DataAttribute.ElementCodename, "])");
var ElementsWithAddButtonSelector = "*[".concat(MetadataAttribute.AddButton, "]");
